@import "normalize.css";
@import "splide.min.css";
html {
  box-sizing: border-box;
}

*,
::after,
::before {
  box-sizing: inherit;
}

body {
  background-color: #ffffff;
  color: #000000;
  font-size: 1rem;
  font-family: "Be Vietnam Pro", "Arial", sans-serif;
  line-height: 1.625rem;
  margin: auto;
}

a {
  color: #000000;
  text-decoration: none;
}

p {
  margin-block: 0.5rem;
}

a:hover {
  color: #000000;
}

h1 {
  font-size: 1.75rem;
  line-height: 2.2rem;
  text-align: center;
  margin-inline: 1rem;
  margin-block: 0;
  padding-block: 1.75rem;
}

h2 {
  text-transform: uppercase;
}

ul,
li {
  list-style-type: none;
  padding-left: 0;
  margin-block: 0;
}

em {
  font-weight: 700;
  font-style: normal;
}

.standard-section {
  margin-top: 0.75rem;
  padding-bottom: 2.375rem;
  padding-inline: 0.625rem;
}

/* navigation bar component - using BEM metodology */
.navbar {
  display: flex;
  align-items: center;
  max-width: 80rem;
  margin-inline: auto;
}
.navbar__desktop-menu-container {
  flex: 1 0 auto;
}
.navbar__desktop-menu {
  display: none;
}
@media (min-width: 925px) {
  .navbar__desktop-menu {
    display: block;
    text-align: center;
  }
}
.navbar__icon {
  padding-inline: 0.6rem;
  padding-top: 1rem;
  padding-bottom: 0.4rem;
}
.navbar__menu-icon {
  background-color: #37a623;
  border: 0;
  padding: 1rem;
}
@media (min-width: 925px) {
  .navbar__menu-icon {
    display: none;
  }
}

/* category menu component - used also for the middle page menu */
.category__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 1.25rem;
}

.category__name {
  font-size: 1.25rem;
  line-height: 1.56rem;
}

.category__list {
  font-size: 1rem;
  line-height: 2.125;
  margin-left: 1.25rem;
  margin-bottom: 1.25rem;
}

/* category list arrow component */
.category__arrow {
  background-image: url("hp-images/navbar-icons/arrow-down.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 16px 8px;
  background-color: #ffffff;
  border: none;
  border-left: 1px solid #dedede;
  height: 2.3rem;
  width: 3.4rem;
}
@media (min-width: 925px) {
  .category__arrow {
    visibility: hidden;
  }
}

/* promo carousel (top of the page) */
.promo-carousel {
  background-color: #f3f3f3;
}

.promo-carousel .splide__slide {
  background-size: cover;
  width: 100%;
  height: 555px;
}

.promo-slide1 {
  background-image: url("hp-images/promo-sliders/maarten-duineveld-pmfJcN7RGiw-unsplash-sh.png");
  background-position: 52% 28%;
}

.promo-slide2 {
  background-image: url("hp-images/promo-sliders/maarten-duineveld-pmfJcN7RGiw-unsplash-sh.png");
  background-position: 70% 28%;
}

.promo-slide3 {
  background-image: url("hp-images/promo-sliders/maarten-duineveld-pmfJcN7RGiw-unsplash-sh.png");
  background-position: 100% 28%;
}

.promo-info {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  gap: 1.25rem;
  min-height: 555px;
  padding-block: 2.9rem;
}

.promo-text {
  font-weight: 700;
  font-size: 2rem;
  line-height: 2.5rem;
  color: #ffffff;
  text-align: center;
  text-shadow: rgba(0, 0, 0, 0.3882352941) 0 3px 13px;
  padding-inline: 1.625rem;
}

.promo-button {
  font-weight: 700;
  font-size: 0.813rem;
  line-height: 1rem;
  color: #ffffff;
  border: none;
  text-align: center;
  text-transform: uppercase;
  background-color: #37a623;
  padding-block: 1rem;
  padding-inline: 1.5rem;
  min-width: 15rem;
}

.promo-carousel .splide__arrow {
  background-image: url("hp-images/promo-sliders/arrow-right.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 14px 28px;
  background-color: transparent;
  height: 2rem;
  width: 2rem;
}
.promo-carousel .splide__arrow.splide__arrow--prev {
  background-image: url("hp-images/promo-sliders/arrow-left.svg");
}

.promo-carousel .splide__arrow svg {
  display: none;
}

.promo-carousel .splide__pagination {
  counter-reset: pagination-num;
  position: static;
  gap: 1.625rem;
  padding-top: 1.125rem;
}

.promo-carousel .splide__pagination__page {
  background: transparent;
}

.promo-carousel .splide__pagination__page::before {
  counter-increment: pagination-num;
  content: counter(pagination-num, decimal-leading-zero);
  font-weight: 400;
  line-height: 1.25rem;
  color: #b1b1b1;
}

.promo-carousel .splide__pagination__page.is-active {
  transform: none;
  background: transparent;
}

.promo-carousel .splide__pagination__page.is-active::before {
  font-weight: 700;
  color: #37a623;
}

/* mobile menu */
.menu {
  background-color: #ffffff;
  display: none;
  position: absolute;
  z-index: 10;
  width: 100%;
}

.menu--visible {
  display: block;
}

/* category menu component - for mobile hamburger menu */
.category__header.category__header--top-menu {
  border-top: 1px solid #dedede;
}

.category__list {
  display: none;
}

.category__list--visible .category__list {
  display: block;
}

.category__list--visible .category__arrow {
  background-image: url("hp-images/navbar-icons/arrow-up.svg");
}

/* other menu items used inside the hamburger - mobile version only */
.menu__items-mobile-only {
  font-size: 0.875rem;
  line-height: 1.625rem;
  border-top: 1px solid #dedede;
  border-bottom: 1px solid #dedede;
  padding-left: 1.25rem;
  padding-block: 1.25rem;
}

/* country selectors */
.country-selectors {
  padding-left: 1.25rem;
  padding-block: 1.25rem;
}

.country-selectors-selector {
  background-color: #f3f3f3;
  border: none;
  font-size: 0.75rem;
  line-height: 1;
  padding-block: 0.25rem;
  padding-inline: 0.4375rem;
  margin: 0.3125rem;
}

/* middle page menu */
.middle-page-menu {
  background-color: #f3f3f3;
  padding-bottom: 2.9375rem;
}

.middle-page-menu-flex {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 925px) {
  .middle-page-menu-flex {
    flex-direction: row;
    gap: 0.875rem;
  }
}

/* category-menu component - extra settings for the middle page menu */
.category--middle-page-menu {
  background-color: #ffffff;
  padding-top: 0.125rem;
  padding-inline: 0.5rem;
  padding-bottom: 0.525rem;
  margin: 0.625rem;
  margin-top: 0;
  width: clamp(20rem, 16.087rem + 22.3602vw, 29rem);
  align-self: center;
}
@media (min-width: 925px) {
  .category--middle-page-menu {
    min-width: 275px;
    max-width: 400px;
    align-self: initial;
  }
}

.category__header--middle-page {
  display: flex;
  align-items: center;
}

.category__name--middle-page {
  font-size: 1.75rem;
  line-height: 1.25;
  margin-top: 1.375rem;
  margin-left: 0.25rem;
  flex: 1 0 auto;
}

.category__name--middle-page-link {
  border-bottom: 2px solid #37a623;
}

@media (min-width: 925px) {
  .category--middle-page-menu .category__list {
    display: block;
  }
}

.category__list--middle-page-link {
  padding: 0.3125rem;
}

.category__list.category__list--middle-page-position {
  position: relative;
  left: 3em;
}

.category__image-skis {
  margin-inline: 0.52rem;
}

/* marketing text section */
.marketing-text {
  background-color: #f3f3f3;
}

.marketing-text-container {
  text-align: center;
  padding-bottom: 2.375rem;
  padding-inline: 0.625rem;
  margin-top: 0;
  display: flex;
  flex-direction: column;
  max-width: 80rem;
  margin-inline: auto;
}
@media (min-width: 425px) {
  .marketing-text-container {
    padding-inline: clamp(0.625rem, -3.0409rem + 13.8012vw, 8rem);
  }
}

/* product badges components */
.product-badges {
  display: flex;
  flex-direction: column;
  align-items: end;
  position: absolute;
  right: 0;
  top: -2rem;
}

.product-badge {
  color: #ffffff;
  font-size: 0.75rem;
  line-height: 2.05;
  padding-inline: 0.7rem;
  margin-top: 0;
  margin-right: 0.5rem;
  margin-bottom: 0.3125rem;
  width: fit-content;
}

.free-delivery {
  background-color: #000000;
}

.recommended {
  background-color: #e19cb3;
}

.new {
  background-color: #9cb5e1;
}

.gift {
  background-color: #e1bf9c;
}

.sale {
  background-color: #e26969;
}

/* hot tips section */
.hot-tips {
  border-bottom: 1px solid #dedede;
}

/* product card component */
.hot-tips-carousel .splide__list.product-card {
  gap: 1rem;
  max-width: 19.25rem;
}

.hot-tips-carousel .splide__track {
  max-width: 80rem;
  margin-top: 0.375rem;
  margin-inline: auto;
}

.hot-tips-carousel .splide__slide {
  margin-top: 2rem;
}

.main-product-image-area {
  background-repeat: no-repeat;
  background-position: center;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 14.25rem;
  margin-top: 1.5rem;
}

.main-product-image {
  width: 90%;
  object-fit: contain;
  height: 100%;
}

.product-info {
  margin-inline: 1.5rem;
}

.product-variants-images {
  display: flex;
  justify-content: center;
  gap: 0.3125rem;
  margin-top: 1.25rem;
}

.product-variants-image-frame {
  width: 2.625rem;
  height: 2.625rem;
  border: 1px solid #dedede;
}
.product-variants-image-frame .product-variants-image {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.product-name {
  font-size: 1.25rem;
  line-height: 1.25;
  text-align: center;
}

.product-old-price {
  font-size: 0.75rem;
  line-height: 1.25;
  text-decoration: line-through;
  color: #b1b1b1;
  text-align: center;
  margin-bottom: 0.3rem;
}

.product-current-price {
  font-size: 1.25rem;
  line-height: 1.25;
  font-weight: 700;
  text-align: center;
  margin-top: 0;
}

.stock-info {
  font-size: 0.8125rem;
  line-height: 1.23;
  font-weight: 700;
  text-transform: uppercase;
  color: #37a623;
  text-align: center;
}

/* hot-tips carousel settings */
.hot-tips-carousel .splide__pagination {
  position: static;
  background-color: #ffffff;
  padding-top: 1.125rem;
}

.hot-tips-carousel .splide__pagination__page {
  width: 0.5625rem;
  height: 0.5625rem;
}

.hot-tips-carousel .splide__pagination__page.is-active {
  transform: none;
  background-color: #37a623;
}

/* bands section*/
.brands {
  border-bottom: 1px solid #dedede;
}

.brands-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
  column-gap: 2rem;
  row-gap: 2rem;
  max-width: 27rem;
  margin-inline: auto;
  cursor: pointer;
}
@media (min-width: 768px) {
  .brands-container {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    max-width: 56rem;
  }
}
.brands-container .brand {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

/* more info button component */
.more-info-button {
  font-size: 1rem;
  line-height: 2.125;
  text-transform: uppercase;
  background-image: url("hp-images/category-images/arrow-category.svg");
  background-repeat: no-repeat;
  background-position: right;
  max-width: max-content;
  display: block;
  padding-right: 0.9325rem;
  margin-inline: auto;
  margin-block: 0.625rem;
}

/* articles */
.article-title {
  font-size: clamp(1.25rem, 1.2rem + 0.25vw, 1.5rem);
  line-height: 1.25;
  display: block;
  margin-top: clamp(0.75rem, 0.625rem + 0.625vw, 1.375rem);
}

.article-image {
  max-width: 100%;
  height: auto;
}

/* .articles carousel settings */
.articles-carousel .splide__slide {
  max-width: clamp(16.44rem, 14.728rem + 8.56vw, 25rem);
  margin-bottom: 1.875rem;
}

.articles-carousel .splide__pagination {
  position: static;
  background-color: #ffffff;
  padding-bottom: 0.7rem;
}
@media (min-width: 1280px) {
  .articles-carousel .splide__pagination {
    display: none;
  }
}

.articles-carousel .splide__pagination__page {
  width: 0.5625rem;
  height: 0.5625rem;
}

.articles-carousel .splide__pagination__page.is-active {
  transform: none;
  background-color: #37a623;
}

.articles-carousel .splide__list {
  justify-content: center;
  gap: 1.4375rem;
}
@media (min-width: 1280px) {
  .articles-carousel .splide__list {
    gap: 2.5rem;
  }
}

/* mailing section */
.mailing {
  background-color: #37a623;
  color: #ffffff;
  text-align: center;
  padding-top: 2rem;
}

.mailing-input {
  width: clamp(15rem, 13.5833rem + 7.0833vw, 19.25rem);
  height: 2.9375rem;
}

.mailing-submit-button {
  background-color: #000000;
  color: #ffffff;
  text-transform: uppercase;
  font-size: 0.813rem;
  line-height: 1rem;
  border: 0;
  padding-block: 1rem;
  padding-inline: 1.5rem;
  margin: 0.375rem;
  width: clamp(6.4375rem, 5.5417rem + 4.4792vw, 9.125rem);
}

.gdpr-link {
  color: #ffffff;
  text-decoration: underline;
}

/* benefit icons section */
.benefit-icons-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
  column-gap: 1.625rem;
  row-gap: 2rem;
  max-width: 27rem;
  margin-block: 3rem;
  margin-inline: auto;
  cursor: pointer;
  align-items: center;
  justify-items: center;
  text-align: center;
}
@media (min-width: 768px) {
  .benefit-icons-container {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    max-width: 56rem;
  }
}

/* footer */
.footer {
  background-color: #000000;
}

.footer-info {
  color: #ffffff;
  display: flex;
  flex-direction: column;
  gap: 4rem;
  padding-top: 1rem;
  padding-inline: 1.875rem;
  padding-bottom: 1.875rem;
  max-width: 80rem;
  margin-inline: auto;
}
@media (min-width: 768px) {
  .footer-info {
    flex-direction: row;
    padding-top: 4.75rem;
    gap: 2rem;
  }
}

.footer-header {
  margin-bottom: 1.875rem;
}

.contact {
  flex: 1 0 auto;
  font-style: normal;
}

.contact-link {
  color: #ffffff;
  font-size: 1.5rem;
  line-height: 1.25;
  font-style: normal;
  display: flex;
  gap: 0.4375rem;
}

.contact-link:hover {
  color: #dedede;
}

.contact-address {
  font-size: 0.9375rem;
  line-height: 1.266rem;
  color: #ffffff;
  font-style: normal;
}

.info-link {
  font-size: 1rem;
  line-height: 2.375;
  color: #dedede;
}